<template>
  <div id="app">
    <img src="./assets/logopoke.png" alt="logo pokemon">
    <h1>¿Quién es ese Pokémon?</h1>
    <p>Pokémon descubiertos: <span :class="discoveredClass">{{ discoveredCount }}</span></p>
    <div class="pokemon-grid">
      <PokemonCard 
        v-for="pokemon in pokemons" 
        :key="pokemon.name" 
        :pokemon="pokemon"
        @pokemon-discovered="incrementDiscoveredCount"
      />
    </div>
  </div>
</template>

<script>
import PokemonCard from './components/PokemonCard.vue';
import axios from 'axios';

export default {
  components: {
    PokemonCard,
  },
  data() {
    return {
      pokemons: [],
      discoveredCount: 0,
    };
  },
  computed: {
    discoveredClass() {
      return this.discoveredCount > 0 ? 'highlighted' : '';
    }
  },
  methods: {
    fetchPokemons() {
      axios.get('https://pokeapi.co/api/v2/pokemon?limit=20')
        .then(response => {
          const pokemonPromises = response.data.results.map(pokemon => 
            axios.get(pokemon.url)
          );
          Promise.all(pokemonPromises).then(pokemonResponses => {
            this.pokemons = pokemonResponses.map(res => res.data);
          });
        });
    },
    incrementDiscoveredCount() {
      this.discoveredCount++;
    }
  },
  created() {
    this.fetchPokemons();
  }
};
</script>

