<template>
    <div class="pokemon-card">
        <img :src="pokemonImage" :class="{ revealed: isRevealed }" alt="imágen de pokémon">
        <div v-if="!isRevealed">
        <input v-model="userInput" @keyup.enter="checkName" placeholder="Nombre del pokémon">
        <button @click="checkName">Descubrir</button>
        </div>
        <div v-else>
        <p>{{ pokemon.name }}</p>
        </div>
    </div>
</template>

<script>
    export default {
    props: ['pokemon'],
    data() {
        return {
        userInput: '',
        isRevealed: false,
        };
    },
    computed: {
        pokemonImage() {
        return this.pokemon.sprites.front_default;
        }
    },
    methods: {
        checkName() {
        if (this.userInput.toLowerCase() === this.pokemon.name.toLowerCase()) {
            this.isRevealed = true;
            this.$emit('pokemon-discovered');
        } else {
            alert('El nombre ingresado es incorrecto.');
        }
        }
    }
};
</script>

<style scoped>
    .pokemon-card img {
    filter: blur(10px);
    }
    .pokemon-card img.revealed {
    filter: none;
    }
    input {
        margin-bottom: 10px;
    }
</style>
